	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-01-27-jeremiah-was-a-bullfrog-lucifer-is-a-cat/index.md absPath of file >>> MarkdownRemark","html":"<p>Whether I like it or not my daughters are big fans of the Disney princess world.</p>\n<p>I am realizing that most things around here are whether I like it or not.</p>\n<p>Anyway, the have seen Cinderella 1, 2 and 3 multiple times. Tonight they were telling I and Callie that they learned about Lucifer today in sunday school. I am sure there teachers enjoyed arguing with them. Lucifer is the cat in Cinderella not the devil.</p>\n<p>Tonight at dinner they were chanting Lucifer, Lucifer, Lucifer - kind of freaky - then was a cat.</p>\n<p>I don't think TV has any influence on kids at all.</p>","frontmatter":{"title":"Jeremiah was a Bullfrog, Lucifer is a Cat","date":"January 27, 2008","image":null}}},"pathContext":{"path":"/jeremiah-was-a-bullfrog-lucifer-is-a-cat"}}

